.wrapper {
  max-width: 950px;
  margin: 0 auto;
  padding-left: 30px;
  padding-right: 30px;
}
@media screen and (max-width: 992px) {
  .wrapper {
    padding-left: 15px;
    padding-right: 15px;
  }
}
@media screen and (max-width: 576px) {
  .wrapper {
    padding-left: 5px;
    padding-right: 5px;
  }
}
h2,
.h2 {
  font-size: 2.5rem;
  text-align: center;
  margin: 20px 0;
}
@media screen and (max-width: 576px) {
  h2,
  .h2 {
    font-size: 2rem;
  }
}
.h2sub {
  text-align: center;
  font-size: 20px;
  margin: 20px 0;
}
.profit {
  background: #232324;
  padding: 15px 0;
}
.profit__title {
  color: #fff;
}
.profit__subtitle {
  color: #6cc372;
}
.profitItems {
  display: flex;
  flex-wrap: wrap;
  margin-left: -5px;
  margin-right: -5px;
  justify-content: space-around;
}
.profitItem {
  text-align: center;
  margin-top: 15px;
  margin-bottom: 15px;
  box-sizing: border-box;
  word-wrap: break-word;
  margin-left: 5px;
  margin-right: 5px;
  width: calc(100% / 24 * 6 - 10px);
  /* .to(380px, {
        width: ~"calc(100% - @{offset})";
    }); 
   */
}
@media screen and (max-width: 992px) {
  .profitItem {
    width: calc(100% / 24 * 9 - 10px);
  }
}
@media screen and (max-width: 576px) {
  .profitItem {
    width: calc(100% / 24 * 20 - 10px);
  }
}
@media screen and (max-width: 380px) {
  .profitItem {
    width: calc(100% / 24 * 24 - 10px);
  }
}
.profitItem__img {
  border-radius: 50%;
}
.profitItem__subtitle {
  color: #fff;
  font-size: 1.5rem;
  margin: 3px 0;
}
.profitItem__text {
  font-size: 16px;
  color: #d94948;
}
.humans {
  background: #6cc372;
  padding: 10px 0;
}
.humans h2 {
  color: #232324;
}
.humans .h2sub {
  color: #fff;
}
.humans .items {
  display: flex;
  flex-wrap: wrap;
  margin-left: -5px;
  margin-right: -5px;
  justify-content: center;
}
.humans .items .item {
  box-sizing: border-box;
  word-wrap: break-word;
  margin-left: 5px;
  margin-right: 5px;
  width: calc(100% / 24 * 6 - 10px);
  border-radius: 10px;
  margin-top: 15px;
  margin-bottom: 15px;
  padding: 10px;
  text-align: center;
  background: #fff;
  display: flex;
  flex-direction: column;
}
@media screen and (max-width: 992px) {
  .humans .items .item {
    width: calc(100% / 24 * 10 - 10px);
  }
}
@media screen and (max-width: 720px) {
  .humans .items .item {
    width: calc(100% / 24 * 11 - 10px);
  }
}
@media screen and (max-width: 576px) {
  .humans .items .item {
    width: calc(100% / 24 * 20 - 10px);
  }
}
@media screen and (max-width: 380px) {
  .humans .items .item {
    width: calc(100% / 24 * 22 - 10px);
  }
}
.humans .items .item img {
  margin: 10px auto;
  width: 90%;
}
.humans .items .item .subtitle {
  color: #6cc372;
  font-size: 24px;
  margin: 10px;
}
@media screen and (max-width: 576px) {
  .humans .items .item .subtitle {
    background: #f90;
    color: #fff;
    border-radius: 5px;
    padding: 5px 0;
  }
}
.humans .items .item .job {
  margin: auto 10px 10px 10px;
}
.humans .items .item .follow {
  background: #6cc372;
  color: #fff;
  font-size: 24px;
  cursor: pointer;
  border-radius: 5px;
  padding: 5px;
}
.portfolio {
  background: #f0f0f0;
  padding: 10px 0;
}
.portfolio h2 {
  color: #232324;
}
.portfolio .h2sub {
  color: #6cc372;
}
.portfolio .items {
  display: flex;
  flex-wrap: wrap;
  margin-left: -5px;
  margin-right: -5px;
  justify-content: space-between;
}
@media screen and (max-width: 720px) {
  .portfolio .items {
    justify-content: center;
  }
}
.portfolio .items .item {
  box-sizing: border-box;
  word-wrap: break-word;
  margin-left: 5px;
  margin-right: 5px;
  /* какой дизайн, такой и код*/
  width: calc(100% / 24 * 5.3 - 10px);
  margin-top: 15px;
  margin-bottom: 15px;
  text-align: center;
}
@media screen and (max-width: 720px) {
  .portfolio .items .item {
    width: calc(100% / 24 * 10 - 10px);
  }
}
@media screen and (max-width: 380px) {
  .portfolio .items .item {
    width: calc(100% / 24 * 22 - 10px);
  }
}
.portfolio .items .item img {
  margin: 10px auto;
  width: 100%;
}
.portfolio .items .item .txt {
  color: #232324;
  font-size: 20px;
}
.pricing .h2sub {
  color: #eb4f4e;
}
.pricing .items {
  display: flex;
  flex-wrap: wrap;
  margin-left: -5px;
  margin-right: -5px;
}
.pricing .items .item {
  text-align: center;
  border-radius: 10px;
  overflow: hidden;
  box-sizing: border-box;
  word-wrap: break-word;
  margin-left: 5px;
  margin-right: 5px;
  width: calc(100% / 24 * 8 - 10px);
}
.pricing .items .item .title {
  background: #fff;
  overflow: hidden;
}
.pricing .items .item ul li {
  background: #f0f0f0;
  padding: 15px 0;
  border-bottom: 1px solid #232324;
}
.pricing .items .item .price {
  background: #eb4f4e;
  color: #fff;
  padding: 10px 15px;
}
.pricing .items .item .price .cur {
  font-size: 1.5em;
}
.pricing .items .item .price .val {
  font-size: 3em;
}
.pricing .items .item .price .per {
  font-size: 1.2em;
  margin-top: 10px;
}
.pricing .items .item .price a {
  color: #232324;
  text-decoration: none;
  background: #fff;
  padding: 5px 20px;
  border-radius: 5px;
  margin-left: auto;
}
footer .items .item {
  background: #aaa;
  min-height: 200px;
}
footer .items .item .row div {
  background: #f50;
}
